'use client'

import { useState, useEffect, use } from 'react'
import { useRouter } from 'next/navigation'
import { ArrowLeft, Plus, X } from 'lucide-react'
import Link from 'next/link'

interface Brand {
  id: string
  name: string
  slug: string
}

interface Product {
  id: string
  name: string
  description: string
  price: number
  category: string
  brand: string
  stock: number
  images: string[]
  is_featured: boolean
  is_active: boolean
  specifications?: any
}

export default function EditProduct({ params }: { params: Promise<{ id: string }> }) {
  const { id } = use(params)
  const router = useRouter()
  const [loading, setLoading] = useState(false)
  const [brands, setBrands] = useState<Brand[]>([])
  const [product, setProduct] = useState<Product | null>(null)

  const [formData, setFormData] = useState({
    name: '',
    description: '',
    price: '',
    category: 'men',
    brand: '',
    stock: '',
    images: [''],
    is_featured: false,
    is_active: true,
    specifications: {
      movement: '',
      case_material: '',
      case_size: '',
      water_resistance: '',
      strap_material: '',
      dial_color: ''
    }
  })

  useEffect(() => {
    fetchProduct()
    fetchBrands()
  }, [id])

  const fetchProduct = async () => {
    try {
      const res = await fetch(`http://localhost:8000/api/products/${id}`)
      if (res.ok) {
        const data = await res.json()
        setProduct(data)
        setFormData({
          name: data.name,
          description: data.description,
          price: data.price.toString(),
          category: data.category,
          brand: data.brand,
          stock: data.stock.toString(),
          images: data.images.length > 0 ? data.images : [''],
          is_featured: data.is_featured,
          is_active: data.is_active,
          specifications: data.specifications || {
            movement: '',
            case_material: '',
            case_size: '',
            water_resistance: '',
            strap_material: '',
            dial_color: ''
          }
        })
      }
    } catch (error) {
      console.error('Error fetching product:', error)
    }
  }

  const fetchBrands = async () => {
    try {
      const res = await fetch('http://localhost:8000/api/brands?is_active=true')
      if (res.ok) {
        const data = await res.json()
        setBrands(data)
      }
    } catch (error) {
      console.error('Error fetching brands:', error)
    }
  }

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) => {
    const { name, value, type } = e.target

    if (type === 'checkbox') {
      const checked = (e.target as HTMLInputElement).checked
      setFormData(prev => ({
        ...prev,
        [name]: checked
      }))
    } else if (name.startsWith('specifications.')) {
      const specField = name.split('.')[1]
      setFormData(prev => ({
        ...prev,
        specifications: {
          ...prev.specifications,
          [specField]: value
        }
      }))
    } else {
      setFormData(prev => ({
        ...prev,
        [name]: value
      }))
    }
  }

  const handleImageChange = (index: number, value: string) => {
    const newImages = [...formData.images]
    newImages[index] = value
    setFormData(prev => ({ ...prev, images: newImages }))
  }

  const addImageField = () => {
    setFormData(prev => ({ ...prev, images: [...prev.images, ''] }))
  }

  const removeImageField = (index: number) => {
    const newImages = formData.images.filter((_, i) => i !== index)
    setFormData(prev => ({ ...prev, images: newImages }))
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setLoading(true)

    const token = localStorage.getItem('token')
    if (!token) {
      alert('Please login')
      router.push('/login')
      return
    }

    try {
      const productData = {
        ...formData,
        price: parseFloat(formData.price),
        stock: parseInt(formData.stock),
        images: formData.images.filter(img => img !== '')
      }

      const res = await fetch(`http://localhost:8000/api/products/${id}`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify(productData)
      })

      if (res.ok) {
        alert('Product updated successfully!')
        router.push('/admin/products')
      } else {
        const error = await res.json()
        alert(error.detail || 'Failed to update product')
      }
    } catch (error) {
      console.error('Error updating product:', error)
      alert('Something went wrong')
    } finally {
      setLoading(false)
    }
  }

  if (!product) {
    return (
      <div style={{ minHeight: '100vh', backgroundColor: '#1a1d2e', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
        <div style={{ color: 'white', fontSize: '1.25rem' }}>Loading product...</div>
      </div>
    )
  }

  return (
    <div style={{ minHeight: '100vh', backgroundColor: '#1a1d2e', padding: '1.5rem' }}>
      <div style={{ maxWidth: '1024px', margin: '0 auto' }}>
        {/* Header */}
        <div style={{ display: 'flex', alignItems: 'center', gap: '1rem', marginBottom: '2rem' }}>
          <Link href="/admin/products">
            <ArrowLeft style={{ width: '1.5rem', height: '1.5rem', color: 'white', cursor: 'pointer' }} />
          </Link>
          <h1 style={{ fontSize: '2rem', fontWeight: 'bold', color: 'white' }}>Edit Product</h1>
        </div>

        {/* Form - Same as Add Product */}
        <form onSubmit={handleSubmit}>
          {/* Copy the same form structure from AddProduct but with formData populated */}
          {/* Basic Information, Images, Specifications sections - same structure */}

          {/* Submit Buttons */}
          <div style={{ display: 'flex', gap: '1rem', justifyContent: 'flex-end' }}>
            <Link href="/admin/products">
              <button
                type="button"
                style={{
                  padding: '0.75rem 2rem',
                  backgroundColor: '#374151',
                  color: 'white',
                  borderRadius: '0.5rem',
                  border: 'none',
                  cursor: 'pointer'
                }}
              >
                Cancel
              </button>
            </Link>
            <button
              type="submit"
              disabled={loading}
              style={{
                padding: '0.75rem 2rem',
                backgroundColor: loading ? '#6B7280' : '#3B82F6',
                color: 'white',
                borderRadius: '0.5rem',
                border: 'none',
                cursor: loading ? 'not-allowed' : 'pointer'
              }}
            >
              {loading ? 'Updating...' : 'Update Product'}
            </button>
          </div>
        </form>
      </div>
    </div>
  )
}